import { BuildContext, Component, HtmlElement, StatelessComponent, StyleBuilder } from "@equantic/runtime";
import { Dictionary } from "./Dictionary";
import { DynamicElement } from "./DynamicElement";

export class Text extends StatelessComponent {
    constructor(content?: any, props?: any) {
        super(props);
        if (content !== undefined) this.content = content;
    }

    build(context: BuildContext) {
        let theme = context.getService('eQuantic.UI.Core.Theme.IAppTheme');let textTheme = theme?.Typography;let attrs = { ["class"]: StyleBuilder.create(textTheme?.Base).push(textTheme?.GetVariant(Variant)).push(this.className).build() };return new DynamicElement({ tagName: this.paragraph ? 'p' : 'span', innerText: this.content, customAttributes: attrs, customEvents: this.buildEvents() });
    }

}

