import { BuildContext, Component, HtmlElement, StatelessComponent, StyleBuilder } from "@equantic/runtime";
import { Dictionary } from "./Dictionary";
import { DynamicElement } from "./DynamicElement";

export class Heading extends StatelessComponent {
    constructor(content?: any, level?: any, props?: any) {
        super(props);
        if (content !== undefined) this.content = content;
        if (level !== undefined) this.level = level;
    }

    build(context: BuildContext) {
        let theme = context.getService('eQuantic.UI.Core.Theme.IAppTheme');let textTheme = theme?.Typography;let attrs = { ["class"]: StyleBuilder.create(textTheme?.Base).push(textTheme?.GetHeading(Level)).push(this.className).build() };return new DynamicElement({ tagName: 'h' + this.level, innerText: this.content, customAttributes: attrs, customEvents: this.buildEvents() });
    }

}

